* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar {
    border: 1px solid red;
    padding: 10px;
    background-color: #04133a;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    /* border: 1px solid blue; */
    display: flex;
    gap: 5px;
    width: fit-content;
}

/* logo-image box */
.logo>div:first-child {
    /* border: 1px solid green; */
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo>div:first-child>img {
    width: 100%;
}

/* logo-text */
.logo>div:last-child>h2 {
    color: white;
}


/* navbar options */
.nav-options {
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-options {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-content: center;
    align-items: center;
}

.nav-options>div{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-options>div>button {
    font-family: 'Josefin Sans', sans-serif;
    border-radius: 4px;
    background-color: #14143a;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 18px;
    padding: 20px 15px;
    transition: all 0.5s;
    cursor: pointer;
}

.nav-options>div>button:hover {
    background-color: #222b69;
}


.nav-options>div>button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.nav-options>div>button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.nav-options>div>button:hover span {
    padding-right: 20px;
}

.nav-options>div>button:hover span:after {
    opacity: 1;
    right: 0;
}
